.corner {
  z-index: 1;
  width: 97px;
  height: 94px;
  background-size: 97px 94px;
  background-image: url(data:image/png;base64,);
}

.corner.left-top,
.corner.left-bottom,
.corner.right-top,
.corner.right-bottom {
  position: absolute;
  pointer-events: none;
}

.corner.left-top {
  left: 0;
  top: 0;
}

.corner.left-bottom {
  left: 0;
  bottom: 0;
  transform: rotate(270deg);
}

.corner.right-top {
  right: 0;
  top: 0;
  transform: rotate(90deg);
}

.corner.right-bottom {
  right: 0;
  bottom: 0;
  transform: rotate(180deg);
}

button.small {
  padding: 0 6px;
}

button {
  padding: 4px 12px;
  background: var(--blur_tint_color);
  border: 1px solid var(--border_color);
  color: var(--main_text_color);
  cursor: pointer;
  border-radius: 4px;
  font-weight: bold;
  font-size: var(--font-size-small);
  transition: all 0.2s ease;

  &:hover:not(:disabled) {
    background: var(--user_mes_blur_tint_color);
  }

  &:hover:not(.active) {
    background: var(--blur_tint_color_less);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &.active {
    background: var(--quote_text_color);
    color: white;
    border-color: var(--quote_text_color);
  }
}

/* 通用主从布局 */
.master-tabs {
  display: flex;
  gap: 4px;
  padding-bottom: 8px;
  border-bottom: 1px solid var(--border_color);
}

.master-detail-body {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 10px;
  flex-grow: 1;
  overflow: hidden;

  .section-title {
    margin-top: 12px;
    margin-bottom: 0;

    &:first-child {
      margin-top: 0;
    }
  }
}

.master-detail-body.reverse {
  grid-template-columns: 1fr 2fr;
}

.master-grid {
  display: grid;
  gap: 10px;
  padding: 10px;
  border-radius: 4px;
  background: var(--blur_tint_color);
  overflow-y: auto;
  max-height: 360px;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  grid-auto-rows: min-content;
  border: 1px solid var(--border_color);
}

.master-grid-item {
  background-color: var(--blur_tint_color_fill);
  border: 1px solid var(--border_color);
  border-radius: 4px;
  text-align: center;
  font-size: var(--font-size-small);
  color: var(--main_text_color);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  overflow: hidden;

  &:hover {
    background-color: var(--blur_tint_color_less);
    transform: scale(1.05);
  }

  &.selected {
    border-color: var(--quote_text_color);
    box-shadow: 0 0 5px var(--quote_text_color);
  }
}

.detail-panel {
  position: relative;
  background: var(--blur_tint_color);
  padding: 15px;
  border-radius: 4px;
  color: var(--main_text_color);
  overflow-y: auto;
  border: 1px solid var(--border_color);

  .details-content {
    display: flex;
    flex-direction: column;
    gap: 8px;

    .edit-form .form-group {
      margin-bottom: 8px;
      display: flex;
      flex-direction: column;
    }

    .edit-form label {
      font-weight: bold;
      margin-bottom: 4px;
      font-size: var(--font-size-medium);
    }

    .edit-form input,
    .edit-form select,
    .edit-form textarea {
      width: 100%;
      padding: 4px;
      border: 1px solid var(--primary_color);
      border-radius: 4px;
      background-color: var(--blur_tint_color);
      color: var(--italics_text_color);
      font-size: var(--font-size-small);
    }

    .edit-form textarea {
      min-height: 80px;
      resize: vertical;
    }

    .edit-buttons {
      margin-top: 12px;
      text-align: right;
    }

    .edit-buttons button {
      margin-left: 8px;
      padding: 4px 8px;
      cursor: pointer;
    }
  }

  .item-name {
    font-size: var(--font-size-medium);
    font-weight: bold;
    margin-bottom: 8px;
  }

  .item-quality {
    font-weight: bold;

    &.common {
      color: #9d9d9d;
    }

    &.good {
      color: #14a700;
    }

    &.excellent {
      color: #0070dd;
    }

    &.masterwork {
      color: #a335ee;
    }

    &.legendary {
      color: #ff8000;
    }
  }

  .item-type-tier,
  .item-slot,
  .detail-item {
    font-size: var(--font-size-small);
    color: var(--italics_text_color);
  }

  .item-description {
    margin-top: 10px;
    font-size: var(--font-size-small);
    line-height: 1.4;
  }
}

/* 通用状态栏和属性栏 */
.attr-points {
  grid-column: 1 / -1;
}

.stat,
.attr {
  font-size: var(--font-size-small);
  color: var(--italics_text_color);
}

.attr {
  display: flex;
  justify-content: space-between;
}

.stat {
  display: grid;
  grid-template-columns: 0.6fr 1fr 0.6fr;
  align-items: center;
  gap: 4px;
  height: 20px;

  .bar {
    height: 12px;
    border: 1px solid var(--border_color);

    .fill {
      height: 100%;
      background-color: var(--quote_text_color);
    }
  }
}

.attributes {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 10px;

  .attr-value {
    display: inline-block;
    width: 20px;
    text-align: center;
  }
}

/* 通用缺省 */
.data-empty {
  font-size: 12px;
  color: rgba(108, 108, 108, 1);
  font-style: italic;
  padding: 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  height: 100%;
}

.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.grid-col-span-full {
  grid-column: 1 / -1;
}
.grid-col-1 {
  grid-template-columns: 1fr;
}
