
@import '../css/style.scss';

.outfit-editor-container {
  padding: 6px;
}

.outfit-meta-editor {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 4px;
  border: 1px solid var(--border_color);
  border-radius: 4px;
  padding: 6px;
}

.outfit-selector-container {
  display: flex;
  gap: 16px;
}

.outfit-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 8px;
  width: 321px;
  flex-shrink: 0;
}

.outfit-slot {
  display: flex;
  align-items: center;
  justify-content: center;
  aspect-ratio: 1 / 1;
  border: 1px dashed var(--border_color);
  color: var(--italics_text_color);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  font-size: var(--font-size-small);

  &:hover {
    border-style: solid;
    color: var(--main_text_color);
  }

  &.selected {
    border-style: solid;
    border-color: var(--quote_text_color);
    color: var(--quote_text_color);
    font-weight: bold;
  }
}

.item-display-panel {
  flex: 1;
  border: 1px solid var(--border_color);
  border-radius: 4px;
  padding: 16px;
  min-width: 200px;
  max-height: 300px;
  overflow-y: auto;

  .item-list {
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      flex-direction: column;
      gap: 8px;
    }
  }

  .item-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid var(--border_color);
    border-radius: 4px;
    padding: 8px;
    color: var(--main_text_color);
    transition: all 0.2s ease-in-out;

    &.equipped {
      border-color: var(--quote_text_color);
      background-color: rgba(86, 57, 137, 0.05);
    }

    button {
      background-color: var(--blur_tint_color);
      border: 1px solid var(--border_color);
      border-radius: 4px;
      padding: 4px 8px;
      cursor: pointer;
      flex-shrink: 0;
      margin-left: 8px;
      &:hover {
        background-color: var(--underline_text_color);
      }
    }
  }

  .item-info {
    font-size: var(--font-size-medium);
  }

  .item-description {
    font-size: var(--font-size-small);
    color: var(--italics_text_color);
    margin-top: 4px;
  }

  .empty-text {
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: center;
    color: var(--italics_text_color);
    font-size: var(--font-size-small);
  }
}
